<template>
  <div class="page">
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="自助服务"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content self-help">
      <div @click="taskAppeal()" class="item-choice"><span>试玩赚钱（付费） - 奖励未到账</span></div>
      <div @click="hiTaskAppeal()" class="item-choice"><span>注册赚钱 - 奖励未到账</span></div>
      <div @click="unfreezeAppeal()" class="item-choice"><span>账号冻结申诉</span></div>

    </div>
  </div>
</template>
<style lang="scss">
  @import '../../../scss/mixins';

  .self-help {
    padding-top: 50px;
    background: #fff !important;

    @include font-regular();

    .item-choice {
      height: 22px;
      line-height: 22px;
      margin: 30px 0 0 20px;
      font-size: 16px;
      color: #484746;
    }
  }

</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index'
  export default {
    components: {
      AnimateHeader
    },

    methods: {
      back () {
        $router.back({
          path: '/help'
        })
      },

      // 试玩任务奖励未到账申诉
      taskAppeal () {
        $router.push('/help/complainSubtask')
      },
      // 高额任务奖励未到账申诉
      hiTaskAppeal () {
        $router.forward({
          path: '/help/complainHitask'
          // query: {backroute: '/dashboard', taskId: 1, taskName: '高额任务', taskDate: '2017-03-09'}
        })
      },
      // 集分宝未到账申诉
      rebateAppeal () {
        $router.forward({
          path: '/help/complainRebate'
        })
      },
      unfreezeAppeal () {
        $router.forward({
          path: '/help/complainUnfreeze'
        })
      }
    }
  }
</script>



